header {
    display: flex;
    flex-direction: column;
    padding-top: 1vw;
    padding-left: 37%;
    padding-bottom: 0.01vw;
    background-color: #3D9970;
  }
  
  .cabecera {
    margin-left: 4%;
    margin-top: 10px;
    padding-top: 1vw;
    color: whitesmoke;
    font-size: 31px;
    font-family: Arial, Helvetica, sans-serif;
    transition: 0.5s font-size linear;
    background-color:  #3D9970;
  }
  
  nav {
    padding-top: 10px;
    justify-content: space-between;
    background-color:  #3D9970;
  }
  
  nav > ul {
    text-align: bottom;
    display: flex;
    list-style-type: none;
  }
  

  
  nav > ul > li {
    text-align: bottom;
    margin-right: 2vw;
  }
  
  nav > ul > li > a {
    text-decoration: none;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 18px;
    color: whitesmoke;
  }
  

  